import React, { useEffect, useReducer } from 'react';
import Navigation from './components/Navigation';
import Field from './components/Field';
import Button from './components/Button';
import ManipulationPanel from './components/ManipulationPanel';
// import useSnakeGame from './hooks/useSnakeGame';
import {
  GameStatus,
  Direction,
  OppositeDirection,
  Delta,
  DirectionKeyCodeMap,
} from './constants';
import {
  initFields,
  isCollision,
  getFoodPosition,
  // isEatingMyself,
} from './utils';
// import { isCompositeComponent } from 'react-dom/test-utils';

const initialPosition = { x: 17, y: 17 };
const initialValues = initFields(35, initialPosition);
const defaultInterval = 100;
let timer = undefined;
const unsubscribe = () => {
  if (!timer) {
    return;
  }
  console.log('unsubscribe');
  clearInterval(timer);
};

const reducer = (state, action) => {
  const { body, fields, status, direction } = state;
  switch (action.type) {
    case 'move':
      if (status !== GameStatus.playing) {
        return state;
      }
      const { x, y } = body[0];
      const delta = Delta[direction];
      const newPosition = {
        x: x + delta.x,
        y: y + delta.y,
      };
      if (isCollision(fields.length, newPosition)) {
        // unsubscribe();
        return { ...state, status: GameStatus.gameover };
      }
      const newBody = [...body];
      const newFields = [...fields];
      if(newFields[newPosition.y][newPosition.x] !== 'food') {
        const removingTrack = newBody.pop();
        newFields[removingTrack.y][removingTrack.x] = '';
      }
      else {
        const food = getFoodPosition(fields.length, [...newBody, newPosition]);
        newFields[food.y][food.x] = 'food';
        // alert('food');
      }
      newFields[newPosition.y][newPosition.x] = 'snake';
      newBody.unshift(newPosition);
      return {
        ...state,
        body: newBody,
        fields: newFields,
      };

    case 'changeDirection':
      if (status !== GameStatus.playing) {
        return state;
      }
      if (OppositeDirection[direction] === action.direction) {
        return state;
      }
      return { ...state, direction: action.direction };

    case 'start':
      return { ...state, status: GameStatus.playing };

    case 'reset':
      return {
        body: [initialPosition],
        fields: initFields(35, initialPosition),
        status: GameStatus.init,
        direction: Direction.up,
      };

    default:
      throw new Error();
  }
};

function App() {
  const [state, dispatch] = useReducer(reducer, {
    body: [initialPosition],
    fields: initialValues,
    status: GameStatus.init,
    direction: Direction.up,
  });

  useEffect(() => {
    timer = setInterval(() => {
      dispatch({ type: 'move' });
    }, defaultInterval);
    return unsubscribe;
  }, []);

  useEffect(() => {
    const handleKeyDown = (e) => {
      const newDirection = DirectionKeyCodeMap[e.keyCode];
      if (!newDirection) {
        return;
      }
      dispatch({ type: 'changeDirection', direction: newDirection });
    };
    document.addEventListener('keydown', handleKeyDown);
    return () => document.removeEventListener('keydown', handleKeyDown);
  }, []);

  const onStart = () => dispatch({ type: 'start' });
  const onRestart = () => dispatch({ type: 'reset' });
  const onChangeDirection = (newDirection) =>
    dispatch({ type: 'changeDirection', direction: newDirection });

  return (
    <div className="App">
      <header className="header">
        <div className="title-container">
          <h1 className="title">Snake Game</h1>
        </div>
        <Navigation />
      </header>
      <main className="main">
        <Field fields={state.fields} />
      </main>
      <footer className="footer">
        <Button status={state.status} onStart={onStart} onRestart={onRestart} />
        <ManipulationPanel onChange={onChangeDirection} />
      </footer>
    </div>
  );
  // const {
  //   body,
  //   difficulty,
  //   fields,
  //   start,
  //   stop,
  //   reload,
  //   status,
  //   updateDirection,
  //   updateDifficulty,
  // } = useSnakeGame();

  // return (
  //   <div className="App">
  //     <header className="header">
  //       <div className="title-container">
  //         <h1 className="title">Snake Game</h1>
  //       </div>
  //       <Navigation
  //         length={body.length}
  //         difficulty={difficulty}
  //         onChangeDifficulty={updateDifficulty}
  //       />
  //     </header>
  //     <main className="main">
  //       <Field fields={fields} />
  //     </main>
  //     <footer className="footer">
  //       <Button
  //         status={status}
  //         onStart={start}
  //         onStop={stop}
  //         onRestart={reload}
  //       />
  //       <ManipulationPanel onChange={updateDirection} />
  //     </footer>
  //   </div>
  // );
}

export default App;
